.Timeline {
    min-height: 100vh;
    display: flex;
    flex-flow: column;
    background: #252827;
    font-size: 16px;
    /* Import */

    @import url('https://fonts.googleapis.com/css?family=Chivo:300,300i,400,400i,700,700i,900,900i|Saira+Extra+Condensed:100,200,300,400,500,600,700,800|Saira:100,200,300,400,500,600,700,800');

    /* Base */
    p {
        font-weight: 300;
    }

    strong {
        font-weight: 600;
    }

    h1 {
        font-family: 'Saira', sans-serif;
        letter-spacing: 1.5px;
        color: white;  
        font-weight: 400;
        font-size: 2.4em;
        text-align: center;
    }

    #content {
        margin-top: 50px;
        text-align: center;  
    }

    /* Timeline */

    .timeline {
        border-left: 4px solid #004ffc;
        border-bottom-right-radius: 4px;
        border-top-right-radius: 4px;    
        background: rgba(255, 255, 255, 0.03);
        color: rgba(255, 255, 255, 0.8);
        font-family: 'Chivo', sans-serif;  
        margin: 50px auto;  
        letter-spacing: 0.5px;   
        position: relative;
        line-height: 1.4em;
        font-size: 1.03em;   
        padding: 50px;   
        list-style: none;
        text-align: left;  
        font-weight: 100;  
        max-width: 30%; 
        
        h1 {
        font-family: 'Saira', sans-serif;
        letter-spacing: 1.5px;
        font-weight: 100;
        font-size: 1.4em;    
        }
        
        h2, h3 {
        font-family: 'Saira', sans-serif;
        letter-spacing: 1.5px;
        font-weight: 400;
        font-size: 1.4em;
        }
        
        .event {
            border-bottom: 1px dashed rgba(255, 255, 255, 0.1);
            padding-bottom: (50px * 0.5);
            margin-bottom: 50px;  
            position: relative;

            &:last-of-type { 
            padding-bottom: 0;
            margin-bottom: 0; 
            border: none;      
            }

            &:before, &:after {
            position: absolute;
            display: block;
            top: 0;
            }

            &:before {
            left: (((120px * 0.6) + 50px + 4px + 11px + (4px * 2)) * 1.5) * -1;    
            color: grey;    
            content: attr(data-date);
            text-align: right;
            font-weight: 100;    
            font-size: 0.9em;
            min-width: 120px;
            font-family: 'Saira', sans-serif;
            }

            &:after {
            box-shadow: 0 0 0 4px #004ffc;    
            left: (50px + 4px + (11px * 0.35)) * -1;        
            background: lighten(#252827,5%);    
            border-radius: 50%;  
            height: 11px;
            width: 11px;
            content: "";
            top: 5px;
            }
        }
    }
}