html {
  min-height: 100%;
}

@font-face {
  font-family: 'Trojan pro';
  font-style: normal;
  font-weight: 400;
  src: url('./assets/fonts/Trajan\ Pro\ Regular.ttf'); /* IE9 Compat Modes */
  src: local('Open Sans'), local('OpenSans'),
       url('./assets/fonts/Trajan\ Pro\ Regular.ttf') format('embedded-opentype'), /* IE6-IE8 */
       url('./assets/fonts/Trajan\ Pro\ Regular.ttf') format('woff2'), /* Super Modern Browsers */
       url('./assets/fonts/Trajan\ Pro\ Regular.ttf') format('woff'), /* Modern Browsers */
       url('./assets/fonts/Trajan\ Pro\ Regular.ttf') format('truetype'), /* Safari, Android, iOS */
}

@font-face {
  font-family: 'Hacker';
  font-style: normal;
  font-weight: 400;
  src: url('./assets/fonts/HACKED.ttf'); /* IE9 Compat Modes */
  src: local('Open Sans'), local('OpenSans'),
       url('./assets/fonts/HACKED.ttf') format('embedded-opentype'), /* IE6-IE8 */
       url('./assets/fonts/HACKED.ttf') format('woff2'), /* Super Modern Browsers */
       url('./assets/fonts/HACKED.ttf') format('woff'), /* Modern Browsers */
       url('./assets/fonts/HACKED.ttf') format('truetype'), /* Safari, Android, iOS */
}


body {
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Hacker';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'trajan-pro-3', 'Trajan Pro', Georgia, 'Times New Roman';
}

*:focus {
  outline: none !important;
  -webkit-tap-highlight-color: transparent;
}

.App {
  text-align: center;
}


